@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
.slider {
    max-width: 100%;
    height: 100vh;
    display: flex;
    box-sizing: border-box;
    align-items: center;
    position: relative;
    overflow: hidden;
    background: transparent;
}

.slide{
    min-width: 100%;
    height: 100%;
    transition: 1000ms;
    overflow: hidden;
}

@media (max-width: 769px) {
    .slider {
        height: 45vh;
    }
}

@media (max-height: 375px) {
    .slider{
        height: 100vh;
    }
}
.images-container { /* OLD - iOS 6-, Safari 3.1-6 */ /* OLD - Firefox 19- (buggy but mostly works) */ /* TWEENER - IE 10 */ /* NEW - Chrome */
  display: flex;
  margin: 10% 8% 5%;
}
.images-container .card img {
  width: 70%;
  border-radius: 45%;
  box-shadow: 2px -4px 9px 8px rgba(83, 56, 6, 0.7);
  margin-top: 30px;
}

.images-container .card {
  color: #d1d1d1;
  width: 37%;
  box-sizing: border-box;
  box-shadow: 0 0 4px #03e9f4, 0 0 10px #03e9f4, 0 0 25px #03e9f4,
    0 0 60px #03e9f4;
  background-color: rgb(252, 50, 28); /* OLD - iOS 6-, Safari 3.1-6 */ /* OLD - Firefox 19- (buggy but mostly works) */ /* TWEENER - IE 10 */ /* NEW - Chrome */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.card h3 {
  text-decoration: underline;
  margin: 0;
  font-size: 2rem;
  font-family: "Lobster", cursive;
}

.card p {
  font-size: 1.3rem;
  letter-spacing: 1px;
  font-style: oblique;
  border-top: 1px double rgba(204, 214, 223, 0.5);
  padding-top: 4px;
  text-align: center;
  letter-spacing: 3px;
  line-height: 25px;
}

#heart {
  position: relative;
  width: 100px;
  height: 90px;
  margin: 300px auto;
  -webkit-animation: heartbeat 4s infinite;
          animation: heartbeat 4s infinite;
}
#heart:before,
#heart:after {
  position: absolute;
  content: "";
  left: 50px;
  top: 0;
  width: 50px;
  height: 80px;
  background: rgb(180, 5, 5);
  border-radius: 50px 50px 0 0;
  transform: rotate(-45deg);
  transform-origin: 0 100%;
}
#heart:after {
  left: 0;
  transform: rotate(45deg);
  transform-origin: 100% 100%;
}

@-webkit-keyframes heartbeat {
  0% {
    transform: scale(0.75);
  }
  20% {
    transform: scale(1);
  }
  40% {
    transform: scale(0.75);
  }
  60% {
    transform: scale(1);
  }
  80% {
    transform: scale(0.75);
  }
  100% {
    transform: scale(0.75);
  }
}

@keyframes heartbeat {
  0% {
    transform: scale(0.75);
  }
  20% {
    transform: scale(1);
  }
  40% {
    transform: scale(0.75);
  }
  60% {
    transform: scale(1);
  }
  80% {
    transform: scale(0.75);
  }
  100% {
    transform: scale(0.75);
  }
}

@media (max-width: 769px) {
  .images-container {
    flex-direction: column;
    align-items: center;
  }
  #heart {
    margin-top: 30px;
    margin-bottom: 30px;
  }

  .images-container .card {
    width: 100%;
  }
  .images-container .card img {
    width: 80%;
    margin-bottom: 1.5rem;
  }

  .card p {
    font-size: 1rem;
  }
}

.backdrop {
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.9);
    z-index: 100;
    top: 0;
    left: 0;
}
.neon-button {
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  position: relative;
  display: inline-block;
  padding: 25px 30px;
  color: #03e9f4;
  text-decoration: none;
  text-transform: uppercase;
  transition: 0.5s;
  letter-spacing: 4px;
  overflow: hidden;
  -webkit-box-redlect: below 1px linear-gradient(transparent, #0005);
}

.neon-button:hover, .neon-button:active {
  background: #03e9f4;
  color: #050801;
  box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4,
    0 0 200px #03e9f4;
}

.neon-button span {
  position: absolute;
  display: block;
}

.neon-button span:nth-child(1) {
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #03e9f4);
  -webkit-animation: animate1 0.6s linear infinite;
          animation: animate1 0.6s linear infinite;
}

@-webkit-keyframes animate1 {
  0% {
    left: -100%;
  }
  50%,
  100% {
    left: 100%;
  }
}

@keyframes animate1 {
  0% {
    left: -100%;
  }
  50%,
  100% {
    left: 100%;
  }
}

.neon-button span:nth-child(2) {
  top: -100%;
  right: 0;
  width: 2px;
  height: 100%;
  background: linear-gradient(180deg, transparent, #03e9f4);
  -webkit-animation: animate2 0.6s linear infinite;
          animation: animate2 0.6s linear infinite;
  -webkit-animation-delay: 0.15s;
          animation-delay: 0.15s;
}

@-webkit-keyframes animate2 {
  0% {
    top: -100%;
  }
  50%,
  100% {
    top: 100%;
  }
}

@keyframes animate2 {
  0% {
    top: -100%;
  }
  50%,
  100% {
    top: 100%;
  }
}

.neon-button span:nth-child(3) {
  bottom: 0;
  right: -100%;
  width: 100%;
  height: 2px;
  background: linear-gradient(270deg, transparent, #03e9f4);
  -webkit-animation: animate3 0.6s linear infinite;
          animation: animate3 0.6s linear infinite;
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}

@-webkit-keyframes animate3 {
  0% {
    right: -100%;
  }
  50%,
  100% {
    right: 100%;
  }
}

@keyframes animate3 {
  0% {
    right: -100%;
  }
  50%,
  100% {
    right: 100%;
  }
}

.neon-button span:nth-child(4) {
  left: 0;
  bottom: -100%;
  width: 2px;
  height: 100%;
  background: linear-gradient(360deg, transparent, #03e9f4);
  -webkit-animation: animate4 0.6s linear infinite;
          animation: animate4 0.6s linear infinite;
  -webkit-animation-delay: 0.45s;
          animation-delay: 0.45s;
}

@-webkit-keyframes animate4 {
  0% {
    bottom: -100%;
  }
  50%,
  100% {
    bottom: 100%;
  }
}

@keyframes animate4 {
  0% {
    bottom: -100%;
  }
  50%,
  100% {
    bottom: 100%;
  }
}
