}

#myH1 .customHeader-div span {
  display: table-cell;
  -webkit-animation: heyyy linear infinite;
          animation: heyyy linear infinite;
}

@-webkit-keyframes heyyy {
  0%, 100% {
    color: #fff;
    -webkit-filter: blur(2px);
            filter: blur(2px);
    text-shadow: 0 0 10px #00b3ff,
    0 0 20px #00b3ff,
    0 0 40px #00b3ff,
    0 0 80px #00b3ff,
    0 0 120px #00b3ff,
    0 0 200px #00b3ff,
    0 0 300px #00b3ff,
    0 0 400px #00b3ff;
  }
  5%, 95% {
    color: #111;
    -webkit-filter: blur(0px);
            filter: blur(0px);
    text-shadow: none;
  }
}

@keyframes heyyy {
  0%, 100% {
    color: #fff;
    -webkit-filter: blur(2px);
            filter: blur(2px);
    text-shadow: 0 0 10px #00b3ff,
    0 0 20px #00b3ff,
    0 0 40px #00b3ff,
    0 0 80px #00b3ff,
    0 0 120px #00b3ff,
    0 0 200px #00b3ff,
    0 0 300px #00b3ff,
    0 0 400px #00b3ff;
  }
  5%, 95% {
    color: #111;
    -webkit-filter: blur(0px);
            filter: blur(0px);
    text-shadow: none;
  }
}

@-webkit-keyframes fill-anime {
  from {
    fill: transparent;
    stroke: transparent;
    box-shadow: none;
  }
  to {
    fill: white;
    stroke: white;
    stroke-width: 4px;
    box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4;
    background: #03e9f4;
  }
}

@keyframes fill-anime {
  from {
    fill: transparent;
    stroke: transparent;
    box-shadow: none;
  }
  to {
    fill: white;
    stroke: white;
    stroke-width: 4px;
    box-shadow: 0 0 5px #03e9f4, 0 0 25px #03e9f4, 0 0 50px #03e9f4;
    background: #03e9f4;
  }
}

